import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import EnrollStudent from '@/components/EnrollStudent'
import StudentInfo from '@/components/StudentInfo'
import finalPage from '@/components/finalPage'
import Login from '@/components/Login'
import Register from '@/components/Register'
import EnrolledStudent from '@/components/EnrolledStudent'
import firebase from 'firebase'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/enrollment',
      name: 'enrollstudent',
      component: EnrollStudent  
    },
    {
      path: '/student_info/:ref_id',
      name: 'studentinfo',
      component: StudentInfo     
    },
    {
      path: '/thankyou/:ref_id',
      name: 'finalpage',
      component: finalPage     
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta:{
        requiresGuest: true
      }      
    },
    {
      path: '/registeraccount',
      name: 'register',
      component: Register,
      meta:{
        requiresAuth: true
      }
    },
    {
      path: '/enrolledstudent',
      name: 'enrolledstudent',
      component: EnrolledStudent,
      meta:{
        requiresAuth: true
      }
    }
  ]
})

router.beforeEach((to,from,next) => {
  //Check for requried auth
  if(to.matched.some(record => record.meta.requiresAuth)){
    //Check if login in firebase
    if(!firebase.auth().currentUser){
      //Go to login page
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    }else{
      //Procced to route
      next()
    }
  }else if(to.matched.some(record => record.meta.requiresGuest)){
    if(firebase.auth().currentUser){
      //Go to dashboard page
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        }
      })
    }else{
      //Procced to route
      next()
    }
  }else{
    next()
  }
})

export default router


