<template>
    <div id="enrollstudent" class="container">
         <h3 class="center bg-primary text-center text-white"> Enrollment Form</h3>
        <div class="row">
            <form @submit.prevent="saveBasicInfo" class="col s12">
            <div class="row">
                <div class="col-sm-12">
                   <div class="form-group">
                        <label for="exampleFormControlSelect1">Old / New Student</label>
                        <select @change="statStd($event)" required class="form-control" id="exampleFormControlSelect1">
                             <option value="" disabled selected>Select</option>
                            <option value="old">Old Student</option>
                            <option value="new">New Student</option>
                        </select>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-sm-12">
                   <div class="form-group">
                        <label for="exampleFormControlSelect1">Incomming Level</label>
                        <select  @change="lvl($event)" required class="form-control" id="exampleFormControlSelect1">
                               <option value="" disabled selected>Select Student Level</option>
                            <option value="k1">Kinder 1</option>
                            <option value="k2">kinder 2</option>
                            <option value="g1">Grade 1</option>
                            <option value="g2">Grade 2</option>
                            <option value="g3">Grade 3</option>
                            <option value="g4">Grade 4</option>
                            <option value="g5">Grade 5</option>
                            <option value="g6">Grade 6</option>
                            <option value="g7">Grade 7</option>
                            <option value="g8">Grade 8</option>
                            <option value="g9">Grade 9</option>
                            <option value="g10">Grade 10</option>
                        </select>
                    </div>
                </div>
            </div>
            <div class="row"  v-if="std_lvl != 'k1' && std_lvl != 'k2'">
                <div class="col-sm-12">
                   <div class="form-group">
                        <label for="exampleFormControlSelect1">Student LRN</label>
                       <input v-model="std_lrn" id="lrn" type="text" class="form-control" required>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-sm-12">
                   <div class="form-group">
                        <label for="exampleFormControlSelect1">Student Classification</label>
                        <select v-model="std_cls" required class="form-control" id="exampleFormControlSelect1">
                            <option value="" disabled selected>Classification</option>
                            <option value="reg">Regular Student</option>
                            <option value="tlc">TLC</option>
                        </select>
                    </div>
                </div>
            </div>  
            <div v-if="std_stat == 'new' && std_lvl != 'k1' && std_lvl != 'k2'">
                <div class="row">
                    <div class="col-sm-12">
                    <h3 class="center bg-primary text-center text-white"> School Information</h3>
                    </div>
                </div>
                <div class="row">
                    <div class="col-sm-12">
                    <input v-model="sch_name" id="School_Name" type="text" class="form-control" required>
                    <label>School Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="col-sm-12">
                    <input v-model="sch_add" id="School_Name" type="text" class="form-control" required>
                    <label>School Address</label>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-4">
                        <div class="card"  v-if="img_psa === '' ">
                            <div class="card-body">
                                <h5 class="card-title">UPLOAD NSO / PSA</h5>
                                <input type="file" @change="onFileChange($event)" required>
                            </div>
                        </div>
                        <div class="card" v-else>
                            <img :src="img_psa">
                            <div class="card-body">
                                <h5 class="card-title">NSO / PSA</h5>
                                 <button class="btn btn-warning" @click="removeImage()">Remove</button>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-4">
                        <div class="card" v-if="sch_card === '' ">
                            <div class="card-body">
                                 <h5 class="card-title">UPLOAD SCHOOL CARD</h5>
                                <input type="file" @change="onFileSC($event)" required>
                            </div>
                        </div>
                        <div class="card" v-else>
                            <img :src="sch_card">
                            <div class="card-body">
                                <h5 class="card-title">SCHOOL CARD</h5>
                                <button class="btn" @click="removeSC()">Remove</button>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-4">
                        <div class="card" v-if="good_moral === '' ">
                            <div class="card-body">
                                <h5 class="card-title">UPLOAD GOOD MORAL</h5>
                                <input type="file" @change="onFileGM($event)" required>
                            </div>
                        </div>
                        <div class="card" v-else>
                            <img :src="good_moral">
                            <div class="card-body">
                                <h5 class="card-title">GOOD MORAL</h5>
                                <button class="btn" @click="removeGM()">Remove</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <hr>
         <div class="row">
             <div class="col">
                <router-link to="/" class="btn btn-danger">CANCEL</router-link>       
             </div>
             <div class="col text-right">
                <button type="submit" class="btn btn-primary pull-right" >SUBMIT</button>  
             </div>
        </div>   
        
        </form>
        </div>
    </div>
</template>

<script>

import db from './firebaseInit'
export default {
    name: 'enrollstudent',
    data(){
        return {
            std_stat: null,
            std_lvl: '',
            std_lrn: '',
            std_cls: '',

            sch_name: '',
            sch_add: '',

            img_psa: '',
            sch_card: '',
            good_moral: '',

            ref_id: null,
            rand_num: null,
            ref_md: null
        }
    },
    mounted (){
        this.rand_num = new Date().getUTCMilliseconds()
        this.ref_md = new Date().getMonth() + ''+ new Date().getDate()
    },
    methods: {
        randomNumber : function(){
            return Math.floor(Math.random() * (10 -1 + 1)) + 1
        },

        statStd : function(event){
          this.std_stat = event.target.value
          if(this.std_stat === 'old'){
            this.ref_id = 'EP' + '01' + this.ref_md + this.rand_num
          }else{
              this.ref_id = 'EP' + '02' + this.ref_md + this.rand_num
          }
          
        },
        lvl : function(event){
            this.std_lvl = event.target.value
        },

        onFileChange(e) {
            var input = e.target
            // Ensure that you have a file before attempting to read it
            if (input.files && input.files[0]) {
                // create a new FileReader to read this image and convert to base64 format
                var reader = new FileReader();
                // Define a callback function to run, when FileReader finishes its job
                reader.onload = (e) => {
                    // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
                    // Read image as base64 and set to imageData
                    this.img_psa = e.target.result;
                    //console.log(e.target.result)
                }
                // Start the reader job - read file as a data url (base64 format)
                reader.readAsDataURL(input.files[0]);

            }
           },
        onFileSC(e) {
            var input = e.target
            // Ensure that you have a file before attempting to read it
            if (input.files && input.files[0]) {
                // create a new FileReader to read this image and convert to base64 format
                var reader = new FileReader();
                // Define a callback function to run, when FileReader finishes its job
                reader.onload = (e) => {
                    // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
                    // Read image as base64 and set to imageData
                    this.sch_card = e.target.result;
                }
                // Start the reader job - read file as a data url (base64 format)
                reader.readAsDataURL(input.files[0]);

            }
           },
        onFileGM(e) {
            var input = e.target
            // Ensure that you have a file before attempting to read it
            if (input.files && input.files[0]) {
                // create a new FileReader to read this image and convert to base64 format
                var reader = new FileReader();
                // Define a callback function to run, when FileReader finishes its job
                reader.onload = (e) => {
                    // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
                    // Read image as base64 and set to imageData
                    this.good_moral = e.target.result;
                }
                // Start the reader job - read file as a data url (base64 format)
                reader.readAsDataURL(input.files[0]);

            }
           },
        
        removeImage: function () {
            this.img_psa = ''; 
            },
         removeSC: function () {
            this.sch_card = ''; 
            },
        removeGM: function () {
            this.good_moral = ''; 
            },
        
        saveBasicInfo () {
            db.collection('studentinfo').add({
                ref_id: this.ref_id,
                std_stat: this.std_stat,
                std_level: this.std_lvl,
                std_lrn: this. std_lrn,
                std_class: this.std_cls,

                prev_school: this.sch_name,
                prev_sch_add: this.sch_add,

                img_psa : this.img_psa,
                img_card: this.sch_card,
                img_gm: this.good_moral
                
            })
            //.then(docRef => this.$router.push('/student_info',{ref_id: this.ref_id}))
            
            .then(docRef => this.$router.push({path: "/student_info/"+this.ref_id}))
            .catch(error => console.log(error))
        }
        
    }
}
</script>


