<template>
    <div id="navbar">
       <header>
        <div class="collapse bg-primary" id="navbarHeader">
            <div class="container">
            <div class="row">
                <div class="col-sm-8 col-md-7 py-4">
                <h4 class="text-white">About</h4>
                <p class="text-white">
                    Basic information about EP Ville Integrated Academy
                </p>
                </div>
                <div class="col-sm-4 offset-md-1 py-4">
                <ul class="list-unstyled">
                    <li v-if="isLogIn" ><router-link to="/enrolledstudent" class="text-white">Enrolled Student</router-link></li>
                    <li v-if="!isLogIn"><router-link to="/login" class="text-white">Sign in</router-link></li>
                    <li v-if="isLogIn" ><router-link to="/registeraccount" class="text-white">Register</router-link></li>
                    <li v-if="isLogIn"><a href="" @click="logout" class="text-white">Logout</a></li>
                </ul>
                </div>
            </div>
            </div>
        </div>
        <div class="navbar navbar-primary bg-primary shadow-sm">
            <div class="container d-flex justify-content-between">
            <router-link to="/" class="navbar-brand d-flex align-items-center text-white">
                <strong>EPVIA</strong>
            </router-link>
            <div v-if="isLogIn" class="text-right">
                <p class="text-white">{{currentUser}}</p>
            </div>
            <button class="navbar-toggler text-white" type="button" data-toggle="collapse" data-target="#navbarHeader" aria-controls="navbarHeader" aria-expanded="false" aria-label="Toggle navigation">
                <i class="fa fa-align-justify"></i>
            </button>
            </div>
        </div>
        </header>
    </div>
</template>
<script>
    import firebase from 'firebase'
    export default{
        name: 'navbar',
        data(){
            return{
                isLogIn:false,
                currentUser:false
            }
        },
        created(){
            if(firebase.auth().currentUser){
                this.isLogIn = true
                this.currentUser = firebase.auth().currentUser.email
            }
        },
        methods: {
            logout(){
                firebase
                .auth()
                .signOut()
                .then(() => {
                    //this.$router.push('/login')
                    this.$router.go({path: this.$router.path})
                })
            }
        }
    }
</script>


